<template>
    <div class="stuworkdetail">
        <div class="crumbs">
            <el-breadcrumb separator="/">
                <el-breadcrumb-item>
                    <i class="el-icon-lx-calendar"></i> 课程设计
                </el-breadcrumb-item>
                <el-breadcrumb-item v-if="items">{{items.teaching.name}}</el-breadcrumb-item>
            </el-breadcrumb> 
        </div>
        <div class="container" v-loading="loading">
            <div class="modify-button" v-if="status==1">
              <el-button type="danger" size="small" @click="handleDelete">退回</el-button>
              <el-button type="primary" @click="editVisible = true" size="small">评分</el-button>
            </div>
            <div class="suc" v-if="status==3">
              <el-alert
                  title="作业已完成"
                  :closable="false"
                  effect="dark"
                  type="success">
                </el-alert>
            </div>
            <div class="suc" v-if="status==2">
              <el-alert
                  title="作业已退回"
                  :closable="false"
                  effect="dark"
                  type="error">
                </el-alert>
            </div>
            <div class="form"  v-if="items">
                <h3>{{items.teaching.name}}</h3>
                <el-form label-width="90px">
                    <el-form-item class="work-date" size="small" label="指导老师：">
                        {{items.teaching.teacherName}}
                    </el-form-item>
                    <el-form-item class="work-date" size="small" label="学生：">
                        {{items.studentName}}
                    </el-form-item>
                    <el-form-item class="work-date" size="small" label="作业项目：">
                        {{items.workProjectInfoDTO.name}}
                    </el-form-item>
                    <el-form-item label="项目简介：">
                        <div class="teacherishi">{{items.workProjectInfoDTO.desc}}</div>
                    </el-form-item>
                    <el-form-item label="相关资料：">
                        <template v-for="item in items.workProjectInfoDTO.filelist"  v-if="item.fileType==1">
                        <a target="_blank" :href="downurls+item.url" class="download-bg"  >
                          <el-button class="download" type="text">{{item.name}}</el-button>
                          <i class="el-icon-download"></i>
                        </a><br />
                        </template>
                    </el-form-item>
                    <el-form-item label="参考答案：">
                        <template v-for="item in items.workProjectInfoDTO.filelist"  v-if="item.fileType==2">
                        <a target="_blank" :href="downurls+item.url" class="download-bg"  >
                          <el-button class="download" type="text">{{item.name}}</el-button>
                          <i class="el-icon-download"></i>
                        </a><br />
                        </template>
                    </el-form-item>
                    <el-form-item label="工艺卡片：">
                        <el-form v-for="(item,index) in craftCardInfoDTOList" :key="index" ref="form" label-width="85px" class="form-in">
                          <h4>工序{{item.sort+1}}</h4>
                          <el-form-item class="work-date" size="small" label="加工表面：">
                              {{item.surfaceName}}
                          </el-form-item>
                          <el-form-item class="work-date" size="small" label="加工形式：">
                              {{item.processingMethodName}}
                          </el-form-item>
                          <el-form-item class="work-date" size="small" label="工序内容：">
                              <div class="teacherishi">{{item.processContent}}</div>
                          </el-form-item>
                        </el-form>
                    </el-form-item>
                    <el-form-item label="作业附件：">
                        <template v-for="item in items.workFileDTOList">
                        <a target="_blank" :href="downurls+item.url" class="download-bg"  >
                          <el-button class="download" type="text">{{item.name}}</el-button>
                          <i class="el-icon-download"></i>
                        </a><br />
                        </template>
                    </el-form-item>
                    <el-form-item label="作业讨论：">
                        <el-tabs type="border-card">
                          <el-tab-pane label="私信老师">
                            <talkCmp @getmore="getmore" @pus="Addmessagebyteacher" @dele="deletemessage" :workMessageInfoDTOList="workMessageInfoDTOList" :total="totalsi"/>
                          </el-tab-pane>
                        </el-tabs>
                    </el-form-item>
                </el-form>
            </div>
        </div>
        <!-- 评分弹出框 -->
        <el-dialog title="评分" :visible.sync="editVisible" width="30%">
            <el-form ref="form" label-width="70px">
                <el-form-item label="评分：">
                    <el-input size="small" onkeyup="this.value=this.value.replace(/[^\d.]/g,'');" v-model="score" maxlength="3"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button size="small" @click="editVisible = false">取 消</el-button>
                <el-button type="primary" size="small" @click="Score">确 定</el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
import {fetchGetworkinfobyid, fetchAddmessagebyteacher, fetchReturn, fetchScore, fetchDeleteteahcermessage,fetchTeacherlistmessage } from '../../axios/requests'
import {downurl} from '../../axios/index'
import talkCmp from '../common/talkCmp.vue'
export default {
    name: 'course',
    components:{talkCmp},
    data() {
        return {
          loading:false,
          status:0,
          id:'',
          items:'',
          craftCardInfoDTOList:[],
          workMessageInfoDTOList:[],

          editVisible:false,
          talktext:'',
          score:'',
          downurls:'',
          totalsi:0,
          startId:0
        };
    },
    mounted(){
      this.downurls = downurl;
      let {id} = this.$route.params;
      this.id = id;
      this.Getworkinfobyid()
      this.Teacherlistmessage(1)
    },
    methods: {
      // 退回操作
      handleDelete() {
          // 二次确认退回
          this.$confirm('确定要退回吗？', '提示', {
              type: 'warning'
          })
          .then(() => {
              this.Return()
          })
          .catch(() => {});
      },
      deletemessage(e){
        this.$confirm('确定要删除吗？', '提示', {
              type: 'warning'
          })
          .then(() => {
              this.Deleteteahcermessage(e.id, e.index);
          })
          .catch(() => {});
      },
      async Deleteteahcermessage(id, index) {//删除消息
        this.loading = true;
        try {
          let response = await fetchDeleteteahcermessage(id);
          if (response.status.code ==0) {
            this.$message({
              showClose: true,
              message: '删除成功',
              type: 'success'
            });
            this.workMessageInfoDTOList.splice(index, 1);
          }else{
            this.$message.error(response.status.description);
          }
        } catch (e) {
          console.log(e);
        } finally {
          this.loading = false;
        }
      },
      async Score() {//作业评分
        let {id, score} = this;
        if (!score) {
          this.$message({
            showClose: true,
            message: '请输入评分',
            type: 'warning'
          });
          return;
        };
        this.loading = true;
        try {
          let response = await fetchScore(id, score);
          if (response.status.code ==0) {
            this.$message({
              showClose: true,
              message: '评分成功',
              type: 'success'
            });
            this.editVisible=false;
            this.Getworkinfobyid()
          }else{
            this.$message.error(response.status.description);
          }
        } catch (e) {
          console.log(e);
        } finally {
          this.loading = false;
        }
      },
      async Return() {//退回作业
        this.loading = true;
        let {id} = this;
        try {
          let response = await fetchReturn(id);
          if (response.status.code ==0) {
            this.$message({
              showClose: true,
              message: '退回成功',
              type: 'success'
            });
            this.Getworkinfobyid()
          }else{
            this.$message.error(response.status.description);
          }
        } catch (e) {
          console.log(e);
        } finally {
          this.loading = false;
        }
      },
      async Addmessagebyteacher(talktext) {//添加作业消息
        let {id} = this;
        if (!talktext) {
          this.$message({
            showClose: true,
            message: '讨论内容不能为空',
            type: 'warning'
          });
          return;
        };
        this.loading = true;
        try {
          let response = await fetchAddmessagebyteacher(id, talktext);
          if (response.status.code ==0) {
            this.$message({
              showClose: true,
              message: '发表成功',
              type: 'success'
            });
            this.talktext = '';
            this.Teacherlistmessage(1)
          }else{
            this.$message.error(response.status.description);
          }
        } catch (e) {
          console.log(e);
        } finally {
          this.loading = false;
        }
      },
      async Getworkinfobyid() {//得到作业详情
        this.loading = true;
        let {id} = this;
        try {
          let response = await fetchGetworkinfobyid(id);
          if (response.status.code ==0) {
            this.items = response.result;
            this.status = response.result.status;
            this.craftCardInfoDTOList = this.$sortBykey(response.result.craftCardInfoDTOList,'sort');
          }else{
            this.$message.error(response.status.description);
          }
        } catch (e) {
          console.log(e);
        } finally {
          this.loading = false;
        }
      },
      getmore(pagenum){
        this.Teacherlistmessage(pagenum)
      },
      async Teacherlistmessage(pageNum) {//得到私信消息
        if (pageNum==1) {
          this.workMessageInfoDTOList=[];
          this.startId=0;
        };
        try {
          let response = await fetchTeacherlistmessage(this.id, pageNum, 10, this.startId);
          if (response.status.code ==0) {
            if (pageNum==1) {
              this.startId = response.result.list[0].id;
             };
            this.totalsi = response.result.total;
             this.workMessageInfoDTOList=response.result.list.reverse().concat(this.workMessageInfoDTOList)
             
          }else{
            this.$message.error(response.status.description);
          }
        } catch (e) {
          console.log(e);
        } finally {
        }
      },
    }
};
</script>

<style lang='scss'>
  .form-in{
    line-height:28px;
    h4{
      padding-left:4px;
    }
    .el-form-item__label,.el-form-item__content{
      line-height:22px;
    }
  }
</style>

<style lang='scss' scoped>
    .stuworkdetail{
      .crumbs{
          padding-left:20px;
        }
        .form{
          color:#444;
          .work-date{
            margin-bottom:0;
          }
          h3{
            font-size:16px;
            margin-bottom:15px;
          }
          .teacherishi{
            line-height:22px;
            padding-top:9px;
          }
          .download-bg{
            color:#409EFF;
            .download{
              padding:0px;
            }
          }
          .form-in{
            border:1px solid #ccc;
            padding:5px 15px 10px;
            background:#f9f9f9;
            margin-top:10px;
            .teacherishi{
              padding-top:5px;
            }
          }
          .talk-bg{
            margin-top:15px;
            
            .talk{
              overflow:hidden;
              line-height:24px;
            }
            div.pic{
              width:24px;
              height:24px;
              background:#ccc;
              border-radius:24px;
              margin-right:6px;
            }
            div.pic img{
              display:block;
              width:24px;
              height:24px;
              border-radius:24px;
            }
            p.fl{
              color:#666;
              font-size:12px;
            }
            .text{
              background:#ffeeee;
              border:1px solid #ffb8b8;
              border-radius:5px;
              width:60%;
              padding:10px 15px;
              margin-left:30px;
              line-height:22px;
              box-sizing:border-box;
            }
            .green{
              background:#f2ffeb;
              border:1px solid #a3e881;
            }
            .self{
              padding-right:30px;
              position: relative;
            }
            .deldiscuss{
              position:absolute;
              right:4px;
              top:4px;
              font-size:16px;
              cursor:pointer;
            }
          }
          .push-talk{
            width:60%;
            margin-left:30px;
            margin-top:30px;
            border:1px solid #ccc;
            border-radius:6px;
          }
          .push-button{
            padding-left:30px;
            margin-top:10px;
          }
        }
        .stu-list{
            border:1px solid #ccc;
        }
        .modify-button{
          position:fixed;
          top:0;
          right:20px;
          z-index:999;
        }
        .suc{
          position:fixed;
          top:0;
          right:0;
          width:200px;
          z-index:999;
        }
    }
</style>


